<template>
  <div id="app">
    <nav class="nav-bar">
      <div class="container">
        <div class="container-left">
          <h3>杜启明个人博客 | 技术博客</h3>
        </div>
        <div class="switch" :class="{activeBg:showNav}" @click="showBgFun">
          <ul>
            <li></li>
            <li></li>
            <li></li>
          </ul>
        </div>
        <div class="container-right" v-show="showNav">
          <ul>
            <router-link v-for="(dataList,index) in navList" :key="index"  :to="dataList.paths">
              <li :class="{activeBg:dataList.active}" @click="activeFun(dataList)">
                <span>{{dataList.text}}</span>
              </li>
            </router-link>
          </ul>
        </div>

      </div>
    </nav>
    <main>
      <router-view></router-view>
    </main>
    <footer>
      <div>
        <h3>Copyright © 2018/3/10 杜启明个人博客 All rights reserved</h3>
      </div>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      showBg:false,
      showNav:false,
      navList:[
        {text:"站点首页",paths:"/home",active:true},
        {text:"关于我",paths:"/about",active:false},
        {text:"web前端",paths:"/web",active:false},
        {text:"Python",paths:"/python",active:false},
        {text:"NodeJs",paths:"/nodejs",active:false},
        {text:"业余爱好",paths:"/hobby",active:false}
      ]
    }
  },
  created(){
    this.body();
  },
  methods:{
    //切换flag
    showBgFun(){
      let temp = this.showNav;
      if(temp===false){
        this.showBg=true;
        this.showNav=true;
        return
      }
      this.showBg=false;
      this.showNav=false;
    },

    body(){
			  let w = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
				if(w>750){
				  this.showNav=true;
        }
			},
    activeFun(data){
      this.navList.forEach(function(obj){
        obj.active = false;
      });
      data.active = !data.active;
      let w = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
      if(w<750){
        this.showNav=false;
      }
    }
    }
}
</script>

<style lang="less">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;

}
@color:#191818;
/** iPad **/
@media only screen and (min-width: 751px){
  body{
    margin-top: 50px;
  }
  .nav-bar{
    width: 100%;
    height: 50px;
    background-color: @color;
    position: fixed;
    top: 0;
    left: 0;
    margin-bottom:50px;
    .container{
      width:80%;
      height: 100%;
      margin: 0 auto;
      .container-left{
        width: 30%;
        height: 100%;
        float: left;
        h3{
          color: #eee;
          font-size: 15px;
          line-height: 50px;
        }
        /*border: 1px solid #000;*/
      }
      .container-right{
        width: 70%;
        /*border: 1px solid #000;*/
        float: left;
        ul{
          text-align: center;
          li{
            float: left;
            padding: 0 5px;
            margin: 0 5px;
            span{
              color: #eee;
              font-size: 13px;
              line-height: 50px;
            }
          }
          li:hover{
            background-color: #3F4042;
          }
        }
      }
    }
  }
  footer{
    div{
      height: 30px;
      width: 100%;
      background-color: @color;
      text-align: center;
      h3{
        color: #eeeeee;
        font-size: 13px;
        line-height: 30px;
      }
    }


  }
}
/** iPhone **/
/*640/20=32 85.3333/32*/
@media only screen and (min-width: 320px) and (max-width: 750px) {
  html { overflow-x: hidden; overflow-y: hidden; }
  body{
    margin-top: 85.3333/32rem;
  }
  .nav-bar{
    width: 100%;
    height: 85.3333/32rem;
    background-color:@color;
    position: fixed;
    top: 0;
    .container{
      width: 100%;
      height: 85.3333/32rem;
      display: -webkit-flex;
      display: flex;
      .container-left{
        -webkit-flex: 8.7;
        flex: 8.7;
        display: -webkit-flex;
        display: flex;
        text-align: center;
        -webkit-align-items: center;
        align-items: center;
        h3{
          color: #eee;
          font-size: 18/32rem;
          /*padding: 0 5px;*/
          -webkit-flex: 1;
          flex: 1;
        }
      }
      .container-right{
        width: 100%;
        z-index: 999;
        position: fixed;
        top: 85.3333/32rem;
        left: 0;
        ul{
          text-align: center;
          li{
            padding: 10/32rem 0;
            margin-top: 3/32rem;
            -webkit-box-shadow: -5/32rem 1/32rem 20/32rem #000;
            box-shadow: -5/32rem 1/32rem 20/32rem #000;
            background-color:@color;
            animation: navAnimation 1s linear;
            -webkit-animation: navAnimation 1s linear;
            span{
              font-size: 16/32rem;
              color: #eee;
            }

          }
        }
      }
      .switch{
        -webkit-flex: 1.3;
        display: -webkit-flex;
        -webkit-align-items:center;
        border: 1px solid #eee;
        border-radius: 10/32rem;
        -webkit-border-radius: 5/32rem;
        ul{
          width: 100%;
          li{
            width: 60%;
            border-bottom: 2px solid #eee;
            margin: 5px auto;
          }
        }
      }
    }
  }
  @-webkit-keyframes navAnimation /* Safari 和 Chrome */
  {
    0% {opacity: 0}
    100% {opacity: 1}
  }@keyframes navAnimation /* Safari 和 Chrome */
  {
    0% {opacity: 0}
    100% {opacity: 1}
  }
  footer{
    div{
      height: 30/32rem;
      width: 100%;
      background-color: @color;
      display: flex;
      display: -webkit-flex;
      text-align: center;
      align-items: center;
      -webkit-align-items: center;
      h3{
        flex: 1;
        -webkit-flex:1;
        color: #eeeeee;
        font-size: 13/32rem;
      }
    }

   }
}

</style>




