<template>
  <div class="clearfloat main">
    <div class="home_left">
      <div class="everyday">
        <ul>
          <li><strong class="txt">每日一句</strong><img src="../assets/img/xiaolaba.gif" alt="小喇叭"></li>
          <li>Change your life today. Don't gamble on the future, act now, without delay.</li>
          <li>从今天起，改变生活；立即行动，拒绝拖延，别把未来当赌注。———— Simone de Beauvoir（翻译来自@妮妮）</li>
        </ul>
      </div>
    </div>
    <div class="home_right" v-if="phone">
      <div class="home_right_me">
        <div class="home_right_me_header"><h3>Tallme</h3></div>
        <div class="home_right_me_body">
          <ul>
            <li><span>QQ:</span><span>961555901</span></li>
            <li><span>邮箱:</span><span>961555901@qq.com</span></li>
            <li><span>github:</span><span>Dwensir</span></li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      phone:true
    }
  },
  created(){
    this.pc_or_phone()
  }
  ,
  methods:{
    pc_or_phone(){
      let w = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
      if(w<=750){
        this.phone=false;
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .clearfloat:after{display:block;clear:both;content:"";visibility:hidden;height:0}
  .clearfloat{zoom:1}
  @-webkit-keyframes  colorlunbo{
    0%{color: #11189A;}
    20%{color: #643A68;}
    40%{color: #FFCD41;}
    60%{color: #A7C0DC;}
    80%{color: #41B983;}
    100%{color: #F18F93;}
  }
  @-ms-keyframes  colorlunbo{
    0%{color: #11189A;}
    20%{color: #643A68;}
    40%{color: #FFCD41;}
    60%{color: #A7C0DC;}
    80%{color: #41B983;}
    100%{color: #F18F93;}
  }
  @-moz-keyframes colorlunbo {
    0%{color: #11189A;}
    20%{color: #643A68;}
    40%{color: #FFCD41;}
    60%{color: #A7C0DC;}
    80%{color: #41B983;}
    100%{color: #F18F93;}
  }@-o-keyframes colorlunbo {
    0%{color: #11189A;}
    20%{color: #643A68;}
    40%{color: #FFCD41;}
    60%{color: #A7C0DC;}
    80%{color: #41B983;}
    100%{color: #F18F93;}
  }
  @keyframes colorlunbo {
    0%{color: #11189A;}
    20%{color: #643A68;}
    40%{color: #FFCD41;}
    60%{color: #A7C0DC;}
    80%{color: #41B983;}
    100%{color: #F18F93;}
  }
  @media only screen and (min-width: 751px){
    .main{
      width: 70%;
      height: 600px;
      margin: 60px auto 10px;
      .home_left{
        width: 70%;
        height: 600px;
        float: left;
        .everyday{
          width: 100%;
          margin: 15px auto;
          text-align: left;
          border-radius: 5px;
          border: 1px solid #e3e3e3;
          -webkit-border-radius: 5px;
          -moz-border-radius: 5px;
          -ms-border-radius: 5px;
          border-radius: 5px;
          background-color: #f5f5f5;
          -webkit-box-shadow: 1px 1px 10px #5f5f5f;
          -moz-box-shadow: 1px 1px 10px #5f5f5f;
          -ms-box-shadow: 1px 1px 10px #5f5f5f;
          box-shadow: 1px 1px 10px #5f5f5f;
          ul{
            margin-top: 15px;
            margin-left: 20px;
            li{
              margin: 10px 0;
              font-size: 13.6px;
              .txt{
                margin-right: 5px;
                -webkit-animation:colorlunbo 20s infinite;
                -moz-animation: colorlunbo 20s infinite;
                -ms-animation: colorlunbo 20s infinite;
                -o-animation: colorlunbo 20s infinite;
                animation: colorlunbo 20s infinite;
              }
            }
          }
        }
      }
      .home_right{
        width: 26%;
        float: right;
        height: 600px;
        .home_right_me{
          width: 100%;
          margin: 15px auto;
          text-align: left;
          border-radius: 8px;
          border: 1px solid #e3e3e3;
          -webkit-border-radius: 5px;
          -moz-border-radius: 5px;
          -ms-border-radius: 5px;
          border-radius: 5px;
          background-color: #1F1F1F;
          -webkit-box-shadow: 1px 1px 10px #5f5f5f;
          -moz-box-shadow: 1px 1px 10px #5f5f5f;
          -ms-box-shadow: 1px 1px 10px #5f5f5f;
          box-shadow: 1px 1px 10px #5f5f5f;
          .home_right_me_header{
            height: 35px;
            background-color: #191818;
            h3{
              line-height: 35px;
              padding-left: 10px;
              color: #eeeeee;
              -webkit-animation:colorlunbo 20s infinite;
              -moz-animation: colorlunbo 20s infinite;
              -ms-animation: colorlunbo 20s infinite;
              -o-animation: colorlunbo 20s infinite;
              animation: colorlunbo 20s infinite;
            }
            }
          .home_right_me_body{
            ul{
              margin-left: 10px;
              li{
                margin: 10px 0;
                color: #eeeeee;
                span{
                  margin: 0 3px;
                  font-size: 13px;
                }
              }
            }
          }
        }
      }
    }
  }
  @media only screen and (min-width: 320px) and (max-width: 750px) {
    .main{
      width: 80%;
      height: 600/32rem;
      display: -webkit-flex;
      display: flex;
      -webkit-align-content: center;
      align-content: center;
      margin: 0 auto;
      .home_left{
        div{
          margin: 15/32rem 0;
        }
        .everyday{
          margin: 15/32rem auto;
          text-align: left;
          border-radius: 5/32rem;
          border: 1/32rem solid #e3e3e3;
          -webkit-border-radius: 5/32rem;
          border-radius: 5/32rem;
          background-color: #f5f5f5;
          -webkit-box-shadow: 1/32rem 1/32rem 10/32rem #5f5f5f;
          box-shadow: 1/32rem 1/32rem 10/32rem #5f5f5f;
          ul{
            margin-top: 15/32rem;
            margin-left: 20/32rem;
            li{
              font-size: 13.6/32rem;
              margin: 10/32rem 0;
              .txt{
                margin-right: 5/32rem;
                animation: colorlunbo 20s infinite;
              }
            }
          }
        }
      }
    }
  }
</style>



